import { evntBus } from "./bus";
import Card from "./Card.vue";

export default {
  data: function () {
    return {
      data: "",
    };
  },
  components: {
    Card,
  },

  methods: {
    get_data() {
      const vm = this;
      frappe.call({
        method: "csf_tz.csf_tz.page.jobcards.jobcards.get_job_cards",
        args: {},
        async: true,
        callback: function (r) {
          if (r.message) {
            vm.data = r.message;
          }
        },
      });
    },
    open_card(item) {
      evntBus.$emit("open_card", item);
    },
    set_status_color(status){
      if (status == "Open") {
        return "status-Open"
      }
      if (status == "Work In Progress") {
        return "status-Work"
      }
      if (status == "Material Transferred") {
        return "status-Material"
      }
      if (status == "On Hold") {
        return "status-Hold"
      }
      if (status == "Submitted") {
        return "status-Submitted"
      }
    }
  },
  created: function () {
    this.get_data();
  },
};
