import { evntBus } from "./bus";
export default {
  data: () => ({
    Dialog: false,
    cardData: "",
  }),
  watch: {},
  methods: {
    close_dialog() {
      this.Dialog = false;
    },
  },
  created: function () {
    evntBus.$on("open_card", (item) => {
      this.Dialog = true;
      this.cardData = item;
    });
  },
};
